.icons {
  display: -ms-flexbox;
  display: flex;
  margin: 0;
}

input[type=radio]:checked + img {
  border: solid 1px black;
  border-radius: 50%;
}

.iconbutton {
  visibility: hidden;
  position: relative;
}

.imgicon {
  width: 40px;
  height: 40px;
  margin: 10px;
  padding: 10px;
}



