.input-container {
  position: absolute;
  top: -200%;
  margin-top: 460px;
  background-color: #e0f2f8;
  z-index: 1;
  border: solid 1px gray;
  width: 90%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.rotate {
  margin-top: -20px;
}

.add-task {
  display: block;
  position: relative;
}

.toggle-input {
  display: none;
}

.toggle-input:checked + .input-container {
  top: 0;
}

.toggle-input:checked +  {
  -webkit-animation: spin 2s infinite; /* Safari 4+ */ /* Fx 5+ */ /* Opera 12+ */
  animation:         spin 2s infinite; /* IE 10+, Fx 29+ */
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}


.day-container-scroll {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 20px;
}

.scrollDown {
  font-family: 'Nunito', sans-serif;
  font-weight: 300;
  border: 1px solid #ccc;
  font-size: 16px;
  height: 34px;
  width: 250px;
  padding: 5px;
  border-radius: 30px;
}

select {
  text-align-last:center;
}

ul {
  padding: 0;
  margin: 0;
}

.progress {
  display: -ms-flexbox;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  width: 600px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

h3 {
  text-align: center;
  margin: auto;
}

#headline {
  font-size: 60px;
  padding: 15px;
  background: url("./images/habit_logo_opacity.png") 0 0 repeat;
  font-weight: 600;
}



